.chartWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px; /* Add some top margin to avoid cutting off the top data points */
}

.chartAreaWrapper {
    width: 100%;
    overflow-x: auto; /* Add horizontal scrollbar if needed */
}

#myChart {
    height: 500px; /* Adjust the height as needed */
    width: 100%; /* Make the chart fill the entire container */
}
